html {
  font-size: 37.5px;
}
html, body, #app, .wrap {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
a {
  text-decoration: none;
  color: #000;
}
.wrap {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  font-size: 0.37333rem;
}
header {
  width: 100%;
  height: 1.17333rem;
  background: #3677E0;
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 15px;
  font-size: 0.45333rem;
}
header > span:nth-child(1) {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
header > span:nth-child(1) > .icon {
  font-size: 29px;
  line-height: 1.17333rem;
}
header > span:nth-child(1) .name {
  padding-left: 0.13333rem;
}
header p span:nth-child(1) {
  display: inline-block;
  width: 1.33333rem;
  height: 0.74667rem;
  border-radius: 0.4rem;
  background: #2565CD;
  text-indent: 0.26667rem;
  line-height: 0.74667rem;
  margin-right: 17px;
}
header p span:nth-child(2) {
  font-size: 0.4rem;
}
header i {
  font-style: normal;
}
.letter {
  position: fixed;
  padding: 0 0.08rem;
  background: #fff;
  opacity: 1;
  display: block;
  height: 533.6px;
  top: 66.7px;
  right: 0;
}
.letter li {
  padding: 0.13333rem;
}
.letter li span {
  display: block;
  height: 15px;
  line-height: 15px;
  color: #266DE2;
  width: 25px;
  text-align: center;
  font-size: 0.32rem;
  text-decoration: none;
}
.content {
  width: 100%;
  height: 100%;
  overflow: hidden;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.content_list {
  width: 100%;
  height: 100%;
  overflow: scroll;
  position: relative;
}
.content_list .car_list {
  width: 100%;
}
.show {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -1.33333rem;
  margin-left: -1.33333rem;
  width: 2.66667rem;
  height: 2.66667rem;
  background: rgba(0, 0, 0, 0.5);
  text-align: center;
  line-height: 2.66667rem;
  border-radius: 0.13333rem;
}
.show span {
  font-size: 1.06667rem;
  color: white;
}
.list {
  width: 100%;
}
.list p {
  font-size: .28rem;
  line-height: .4rem;
  background: #f4f4f4;
  padding-left: .3rem;
  color: #aaa;
}
.list ul {
  width: 100%;
}
.list ul li {
  padding: 0.26667rem 0.4rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
  border-bottom: 1px solid #eee;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.list ul li span:nth-child(1) {
  width: 40px;
  height: 40px;
  display: inline-block;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.list ul li span:nth-child(1) img {
  width: 100%;
  height: auto;
}
.list ul li span:nth-child(2) {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-left: 0.4rem;
}
.another {
  width: 100%;
  height: 100%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -webkit-transition: .6s transform;
  transition: .6s transform;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.another .left {
  -webkit-box-flex: 3;
      -ms-flex: 3;
          flex: 3;
  background: rgba(0, 0, 0, 0.3);
  position: relative;
}
.another .left span {
  position: absolute;
  left: 50%;
  top: 50%;
  color: white;
}
.another .left span i {
  font-size: 1.33333rem;
}
.another .right {
  -webkit-box-flex: 7;
      -ms-flex: 7;
          flex: 7;
  overflow: scroll;
  background: white;
}
.right_list {
  width: 100%;
}
.right_list > p {
